#strategies-wrapper {
    margin: 20px 0;
}

.strategies-list {
    margin-bottom: 20px;
    list-style-type: none;
    padding: 0;
}

.strategies-list li {
    margin-bottom: 0px;
    display: flex;
    align-items: center;
}

.strategies-list span {
    margin-right: 10px;
}

.strategies-list input[type="text"] {
   margin-right: 10px;
   margin-bottom: 10px;
}

.strategies-list .button {
    padding: 0px 5px;
    cursor: pointer;
}

.add-strategy {
    margin-top: 10px;
    background: #007bff;
    color: white;
}

.add-strategy:hover {
    background: #0056b3;
}

.remove-strategy {
    position: relative;
    padding: 5px;
}

.strategy-label{
    cursor: pointer;
}

.remove-strategy:hover {
}
